import { getComments, addComment } from '@/api/article'

export default {
  name: 'ArticleComments',
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      comments: [], // 文章列表,
      comment: {
        body: ''
      },
      disable: false,
    }
  },
  async mounted () {
    const { data } = await getComments(this.article.slug)
    this.comments = data.comments
  },
  methods: {
      async addComment(){
          this.disable = true;
          await addComment({
              comment: this.comment,
              slug: this.article.slug
          })
          this.disable = false;
          this.comment.body = "";
          const { data } = await getComments(this.article.slug)
          this.comments = data.comments
      },
  },
}
